export function CyborgCard(props) {
  const cardRef = useRef(null);

  const x = useMotionValue(0);
  const y = useMotionValue(0);
  const rotateX = useTransform(y, [-100, 100], [30, -30]);
  const rotateY = useTransform(x, [-100, 100], [-30, 30]);

  function updateReflection(degree, percentage) {
    return `linear-gradient(${degree}deg, rgba(255,255,255,0) 0%,rgba(255,255,255,0.5) ${percentage}%,rgba(255,255,255,0) 100%)`;
  }

  useEffect(() => {
    function handleOrientation(event) {
      const { alpha, beta, gamma } = event;
      x.set(gamma * 3);
      y.set(beta * 3);

      const degree = gamma;
      const percentage = beta > 0 ? (beta / 180) * 100 : 0;

      if (cardRef.current) {
        cardRef.current.style.background = updateReflection(degree, percentage);
        cardRef.current.style.backgroundSize = "cover";
      }
    }

    window.addEventListener('deviceorientation', handleOrientation);

    return () => {
      window.removeEventListener('deviceorientation', handleOrientation);
    };
  }, [x, y]); // AsegÃºrate de que este efecto se ejecute solo una vez al montar el componente
  
  return (
    <CardWrapper className="cyborg-card">
      <CardContainer
        style={{ x, y, rotateX, rotateY, z: 100 }}
        drag
        dragElastic={0.26}
        dragConstraints={{ top: 0, left: 0, right: 0, bottom: 0 }}
        whileTap={{ cursor: "grabbing" }}
      >
        <Shine style={{ x, y, rotateX, rotateY, z: 100 }} />
        <TopContainer>

          <Island> </Island>

          <CircleWrapper>
            <Circle />
          </CircleWrapper>

          <ShoesWrapper2>
            <Extras
              style={{ x, y: y.get() + 50, rotateX, rotateY, rotate: "0deg", z: 50000 }} // Ajusta el valor de y para cambiar la posiciÃ³n vertical
              drag
              dragElastic={0.02}
              dragConstraints={{ top: 0, left: 0, right: 0, bottom: 0 }}
              whileTap={{ cursor: "grabbing" }}
            >
              <img src={extrasImg} />
            </Extras>
          </ShoesWrapper2>

          <ShoesWrapper>
            <Shoes
              style={{ x, y, rotateX, rotateY, rotate: "0deg", z: 100000 }}
              drag
              dragElastic={0.36}
              dragConstraints={{ top: 0, left: 0, right: 0, bottom: 0 }}
              whileTap={{ cursor: "grabbing" }}
            >
              <GradientBorder/>
                  <StaticImageWrapper href="/descubre"> {/*    https://alibhtty.web.app/go/shorts/     ./shorts/ */}
                    <img
                      src={cover4bImg}
                      alt="Cyborg"
                      style={{
                        backgroundColor:"#111111",
                        border:".4em solid rgb(0,0,0,1)",
                        width: "100%",
                        height: "100%",
                        borderRadius: "50%",
                      }}
                    />
                  </StaticImageWrapper>
            </Shoes>
          </ShoesWrapper>

          <TitleText>ali bhtty</TitleText>
        </TopContainer>
        <BottomContainer>
          <ShoesDetails />
        </BottomContainer>
      </CardContainer>
    </CardWrapper>
  );
}